.projectbox {
  padding: 0 10px;
  width: 45%;
  height: 400px;
  float: left;
}
.projectbox img {
    width: 90%;
    border: 1px solid #eee;
    border-radius: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}
.projectbox .small {
    color: #deeef5;
}
.projectbox h3 {
    font-size: 15px;
    color: white;
