html, body {
  margin: 0;
  height: 100%;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.home-section {
  display: table;
  height: 700px;
  width: 100%;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(#227daa), to(#51accd));
  background: linear-gradient(to bottom, #227daa 0%, #51accd 100%);
}
.home-section .introduction {
    vertical-align: middle;
    display: table-cell;
    padding: 10px 100px;
}
.home-section .introduction .name {
      font-size: 5em;
      color: white;
      margin: 0;
}
.home-section .introduction .introduce {
      color: white;
}
.whitebg-section {
  display: block;
}
.whitebg-section .title h1 {
    color: #3295be;
}
.whitebg-section .title h1::after {
      border: 2px solid #3295be;
}
.title {
  width: 20%;
  float: left;
}
.title h1 {
    margin-top: 0px;
    margin-bottom: 16px;
}
.title h1::after {
      content: ' ';
      display: block;
      width: 50px;
      margin: 10px 0;
}
.title img {
    margin: 0 auto;
}
.content {
  float: left;
  padding: 0px 50px;
  width: 80%;
  display: block;
}
.content h2 {
    color: #58798c;
}
.content p {
    margin: 0;
    color: #58798c;
    font-size: 18px;
    line-height: 30px;
}
.section-content {
  padding: 100px 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.small {
  font-size: 14px;
  color: #aac7d9;
  margin-bottom: 5px;
}
.box {
  border: 1px solid #89bbd3;
  border-radius: 10px;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}
.box h2 {
    display: inline;
}
.bluebg-section {
  background: -webkit-gradient(linear, left top, left bottom, from(#227daa), to(#51accd));
  background: linear-gradient(to bottom, #227daa 0%, #51accd 100%);
}
.bluebg-section .title h1 {
    color: white;
}
.bluebg-section .title h1::after {
      border: 2px solid white;
}
.bluebg-section h2 {
    color: white;
    text-transform: none;
}
.tag {
  border: 1px solid #ddd;
  display: inline-block;
  border-radius: 6px;
  padding: 8px 10px;
  font-size: 12px;
  color: #deeef5;
  margin-right: 5px;
  margin-bottom: 10px;
}

